(function() {
    'use strict';

    angular
        .module('oncoscape')
        .run(runBlock);

    /** @ngInject */
    function runBlock($rootScope, $state, $window, $exceptionHandler, osApi, $log) {
        $log.log("  ___  _ __   ___ ___  ___  ___ __ _ _ __   ___ \n / _ \\| '_ \\ / __/ _ \\/ __|/ __/ _` | '_ \\ / _ \\\n| (_) | | | | (_| (_) \\__ \\ (_| (_| | |_) |  __/\n \\___/|_| |_|\\___\\___/|___/\\___\\__,_| .__/ \\___|\n                                    |_|         ");

        // Route Errors To Angular
        $window.onerror = function handleGlobalError( message, fileName, lineNumber, columnNumber, error ) {
            if ( ! error ) {
                error = new Error( message );
                error.fileName = fileName;
                error.lineNumber = lineNumber;
                error.columnNumber = ( columnNumber || 0 );
            }
            $exceptionHandler( error );
        }




        // Actions To Take On State Change
        $rootScope.$on('$stateChangeStart', function(event, toState) {

            // Hide Busy Cursor
            osApi.setBusy(false);

            // Reset DataTable Custom Filters
            angular.element.fn.DataTable.ext.search = [];

            // Route unauthenticated users to landing page
            // if (toState.authenticate && !osApi.getUserApi().getUser().authenticated) {
            //      $state.transitionTo("landing");
            //      event.preventDefault();
            // }
        });
    }
})();
(function() {
    'use strict';

    angular
        .module('oncoscape')
        .config(routerConfig);

    /** @ngInject */
    function routerConfig($stateProvider, $urlRouterProvider) {


        $stateProvider
            .state('landing', {
                url: '/',
                template: '<os-landing>',
                authenticate: false
            })
            .state('help', {
                url: '/help',
                template: '<os-help>',
                authenticate: false
            })
            .state('datasource', {
                url: '/datasource',
                template: '<os-datasource>',
                authenticate: true
            })
            .state('tools', {
                url: '/tools/{datasource}',
                template: '<os-tools>',
                authenticate: true
            })
            .state('metadata', {
                url: '/metadata/{datasource}',
                template: '<os-metadata>',
                authenticate: true
            })
            .state('history', {
                url: '/history/{datasource}',
                template: '<os-history>',
                authenticate: true
            })
            .state('plsr', {
                url: '/plsr/{datasource}',
                template: '<os-plsr>',
                authenticate: true
            })
            .state('pca', {
                url: '/pca/{datasource}',
                template: '<os-pca>',
                authenticate: true
            })
            .state('pca3d', {
                url: '/pca3d/{datasource}',
                template: '<os-pca3d>',
                authenticate: true
            })
            .state('markers', {
                url: '/markers/{datasource}',
                template: '<os-markers>',
                authenticate: true
            })
            .state('pathways', {
                url: '/pathways/{datasource}',
                template: '<os-pathways>',
                authenticate: true
            })
            .state('timelines', {
                url: '/timelines/{datasource}',
                template: '<os-timelines>',
                authenticate: true
            })
            .state('survival', {
                url: '/survival/{datasource}',
                template: '<os-survival>',
                authenticate: true
            })
            .state('compare', {
                url: '/compare/{datasource}',
                template: '<os-compare>',
                authenticate: true
            });

        $urlRouterProvider.otherwise('/');
    }

})();

